exports.push([module.id, "\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n    .vue-table {\n\n    }\n\n    /*#maindiv {\n        content: \" \";\n        box-sizing: border-box;\n        display:\n        table; width: 100%;\n    }\n*/\n    .vue-table-loading .spinner {\n        border: 16px solid #f3f3f3; /* Light grey */\n        border-top: 16px solid #3498db; /* Blue */\n        border-radius: 50%;\n        width: 120px;\n        height: 120px;\n        animation: spin 2s linear infinite;\n        position: absolute;\n        left: 50%;\n        top: 50%;\n        margin: -60px 0 0 -60px;\n    }\n\n    @keyframes spin {\n        0% { transform: rotate(0deg); }\n        100% { transform: rotate(360deg); }\n    }\n\n\n    .vue-table-loading{\n        position: absolute;\n        z-index: 99;\n        background-color: #ddd;\n        opacity: 0.5;\n        width: 100%;\n        height: 100%;\n    }\n\n    .vue-table-loading-hidden {\n        display: none;\n    }\n\n    table.vue-table thead > tr > th {\n        cursor: pointer;\n        padding-right: 30px !important;\n    }\n\n    /*.vue-table th.active {\n        color: red;\n    }*/\n\n    .vue-table .icon::before {\n        display: inline-block;\n        font-style: normal;\n        font-variant: normal;\n        text-rendering: auto;\n        -webkit-font-smoothing: antialiased;\n    }\n\n    .vue-table .arrow {\n        opacity: 1;\n        position: relative;\n    }\n\n    .vue-table .arrow:before {\n        position: absolute;\n        bottom: 8px;\n        right: 8px;\n        display: block;\n        font-family: \"Font Awesome 5 Free\";\n        font-weight: 900;\n        content: \"\\F0DC\";\n        /*\n        display: inline-block;\n        vertical-align: middle;\n        width: 0;\n        height: 0;\n        margin-left: 5px;\n        opacity: 0.66;*/\n    }\n\n    .vue-table .arrow.asc:before {\n        content: \"\\F0DE\";\n        /*\n        border-left: 4px solid transparent;\n        border-right: 4px solid transparent;\n        border-bottom: 4px solid #000;\n        */\n    }\n\n    .vue-table .arrow.dsc:before {\n        content: \"\\F0DD\";\n    }\n\n\n    .vue-table .editableField {\n        cursor:pointer;\n    }\n\n    /*.vue-table .selected-cell {\n        background-color: #F7C072;\n    }\n\n    .vue-table .selected-row {\n        background-color: #FAE1BE !important;\n    }*/\n", "", {"version":3,"sources":["/./src/VueBootstrapTable.vue?e26c92b6"],"names":[],"mappings":";;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;IA2GA;;KAEA;;IAEA;;;;;;EAMA;IACA;QACA,2BAAA,CAAA,gBAAA;QACA,+BAAA,CAAA,UAAA;QACA,mBAAA;QACA,aAAA;QACA,cAAA;QACA,mCAAA;QACA,mBAAA;QACA,UAAA;QACA,SAAA;QACA,wBAAA;KACA;;IAEA;QACA,KAAA,wBAAA,EAAA;QACA,OAAA,0BAAA,EAAA;KACA;;;IAGA;QACA,mBAAA;QACA,YAAA;QACA,uBAAA;QACA,aAAA;QACA,YAAA;QACA,aAAA;KACA;;IAEA;QACA,cAAA;KACA;;IAEA;QACA,gBAAA;QACA,+BAAA;KACA;;IAEA;;OAEA;;IAEA;QACA,sBAAA;QACA,mBAAA;QACA,qBAAA;QACA,qBAAA;QACA,oCAAA;KACA;;IAEA;QACA,WAAA;QACA,mBAAA;KACA;;IAEA;QACA,mBAAA;QACA,YAAA;QACA,WAAA;QACA,eAAA;QACA,mCAAA;QACA,iBAAA;QACA,iBAAA;QACA;;;;;;wBAMA;KACA;;IAEA;QACA,iBAAA;QACA;;;;UAIA;KACA;;IAEA;QACA,iBAAA;KACA;;;IAGA;QACA,eAAA;KACA;;IAEA;;;;;;OAMA","file":"VueBootstrapTable.vue","sourcesContent":["<template>\r\n    <div class=\"container-fluid\">\r\n        <!--<pre>{{columns}}</pre>-->\r\n        <!--<pre>{{$data}}</pre>-->\r\n        <div class=\"row\">\r\n            <div class=\"col-6\">\r\n                <div v-if=\"showFilter\" style=\"padding-top: 10px;padding-bottom: 10px;\">\r\n                    <div class=\"input-group\">\r\n                        <input type=\"text\" class=\"form-control\" placeholder=\"Filter\" v-model=\"filterKey\">\r\n                        <!--<div class=\"input-group-append\">\r\n                            <span class=\"input-group-text fa fa-search\"></span>\r\n                        </div>-->\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"col-6\">\r\n                <div v-if=\"showColumnPicker\" style=\"padding-top: 10px;padding-bottom: 10px;float:right;\">\r\n                    <div class=\"btn-group\">\r\n                        <button type=\"button\" class=\"btn btn-outline-primary dropdown-toggle\" data-toggle=\"dropdown\"\r\n                                aria-haspopup=\"true\">\r\n                            Columns <span class=\"caret\"></span>\r\n                        </button>\r\n                        <div class=\"dropdown-menu dropdown-menu-right\">\r\n                            <button v-for=\"column in displayCols\"\r\n                               class=\"dropdown-item\"\r\n                               @click.stop.prevent=\"toggleColumn(column)\"\r\n                            >\r\n                                <i v-if=\"column.visible\" class=\"fa fa-check\"></i> {{column.title}}\r\n                            </button>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <div class=\"row\">\r\n            <div class=\"col-sm-12\">\r\n                <div id=\"loadingdiv\" :class=\"{'vue-table-loading': this.loading , 'vue-table-loading-hidden': !this.loading}\">\r\n                    <div class=\"spinner\"></div>\r\n                </div>\r\n                <table class=\"table table-bordered table-hover table-condensed table-striped vue-table\">\r\n                    <thead>\r\n                    <tr>\r\n                        <th v-if=\"selectable\">\r\n                            <div class=\"custom-control custom-checkbox\">\r\n                                <input type=\"checkbox\" class=\"custom-control-input\" id=\"checkAll\" aria-label=\"Select All\" v-model=\"allSelected\">\r\n                                <label class=\"custom-control-label\" for=\"checkAll\"></label>\r\n                            </div>\r\n                            <!--<div class=\"form-check\">\r\n                                <input class=\"form-check-input position-static\" type=\"checkbox\" aria-label=\"Select All\" v-model=\"allSelected\">\r\n                            </div>-->\r\n                        </th>\r\n                        <th v-for=\"column in displayColsVisible\" @click=\"sortBy($event, column.name, column.sortable)\"\r\n                            track-by=\"column\"\r\n                            class=\"icon\"\r\n                            :class=\"getClasses(column)\">\r\n                            {{ column.title }}\r\n                        </th>\r\n                    </tr>\r\n                    </thead>\r\n                    <tbody>\r\n                    <tr v-for=\"(entry, index) in filteredValuesSorted \" track-by=\"entry\" @click=\"rowClickHandler($event, entry)\">\r\n                        <td v-if=\"selectable\">\r\n                            <div class=\"custom-control custom-checkbox\">\r\n                                <input type=\"checkbox\" class=\"custom-control-input\" :id=\"'check'+index\" v-model=\"entry.selected\">\r\n                                <label class=\"custom-control-label\" :for=\"'check'+index\"></label>\r\n                            </div>\r\n                            <!--<div class=\"form-check\">\r\n                                <input class=\"form-check-input position-static\" type=\"checkbox\" aria-label=\"Select All\" v-model=\"entry.selected\">\r\n                            </div>-->\r\n                        </td>\r\n                        <td v-for=\"column in displayColsVisible\" track-by=\"column\"\r\n                            v-show=\"column.visible\" :class=\"column.cellstyle\">\r\n                            <slot :name=\"column.name\" :column=\"column\" :value=\"entry\">\r\n                                <span v-if=\"column.renderfunction!==false\" v-html=\"column.renderfunction( column.name, entry )\"></span>\r\n                                <span v-else-if=\"!column.editable\">{{ entry[column.name] }}</span>\r\n                                <value-field-section v-else\r\n                                                     :entry=\"entry\"\r\n                                                     :columnname=\"column.name\"></value-field-section>\r\n                            </slot>\r\n                        </td>\r\n                    </tr>\r\n                    </tbody>\r\n                </table>\r\n            </div>\r\n            <div v-if=\"paginated\" class=\"col-sm-12\">\r\n                <div class=\"btn-toolbar\" role=\"toolbar\" aria-label=\"pagination bar\">\r\n                    <div class=\"btn-group mr-2\" role=\"group\" aria-label=\"first page\">\r\n                        <button type=\"button\" class=\"btn btn-outline-primary\" @click=\"page=1\">&laquo;</button>\r\n                    </div>\r\n                    <div class=\"btn-group mr-2\" role=\"group\" aria-label=\"pages\">\r\n                        <button v-for=\"index in validPageNumbers\"\r\n                                type=\"button\" class=\"btn btn-outline-primary\"\r\n                                :class=\"{ active: page===index }\"\r\n                                @click=\"page=index\">\r\n                            {{index}}\r\n                        </button>\r\n                    </div>\r\n                    <div class=\"btn-group mr-2\" v-if=\"showPaginationEtc\">...</div>\r\n                    <div class=\"btn-group\" role=\"group\" aria-label=\"last page\">\r\n                        <button type=\"button\" class=\"btn btn-outline-primary\" @click=\"page=maxPage\">&raquo;</button>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n<style>\r\n    .vue-table {\r\n\r\n    }\r\n\r\n    /*#maindiv {\r\n        content: \" \";\r\n        box-sizing: border-box;\r\n        display:\r\n        table; width: 100%;\r\n    }\r\n*/\r\n    .vue-table-loading .spinner {\r\n        border: 16px solid #f3f3f3; /* Light grey */\r\n        border-top: 16px solid #3498db; /* Blue */\r\n        border-radius: 50%;\r\n        width: 120px;\r\n        height: 120px;\r\n        animation: spin 2s linear infinite;\r\n        position: absolute;\r\n        left: 50%;\r\n        top: 50%;\r\n        margin: -60px 0 0 -60px;\r\n    }\r\n\r\n    @keyframes spin {\r\n        0% { transform: rotate(0deg); }\r\n        100% { transform: rotate(360deg); }\r\n    }\r\n\r\n\r\n    .vue-table-loading{\r\n        position: absolute;\r\n        z-index: 99;\r\n        background-color: #ddd;\r\n        opacity: 0.5;\r\n        width: 100%;\r\n        height: 100%;\r\n    }\r\n\r\n    .vue-table-loading-hidden {\r\n        display: none;\r\n    }\r\n\r\n    table.vue-table thead > tr > th {\r\n        cursor: pointer;\r\n        padding-right: 30px !important;\r\n    }\r\n\r\n    /*.vue-table th.active {\r\n        color: red;\r\n    }*/\r\n\r\n    .vue-table .icon::before {\r\n        display: inline-block;\r\n        font-style: normal;\r\n        font-variant: normal;\r\n        text-rendering: auto;\r\n        -webkit-font-smoothing: antialiased;\r\n    }\r\n\r\n    .vue-table .arrow {\r\n        opacity: 1;\r\n        position: relative;\r\n    }\r\n\r\n    .vue-table .arrow:before {\r\n        position: absolute;\r\n        bottom: 8px;\r\n        right: 8px;\r\n        display: block;\r\n        font-family: \"Font Awesome 5 Free\";\r\n        font-weight: 900;\r\n        content: \"\\f0dc\";\r\n        /*\r\n        display: inline-block;\r\n        vertical-align: middle;\r\n        width: 0;\r\n        height: 0;\r\n        margin-left: 5px;\r\n        opacity: 0.66;*/\r\n    }\r\n\r\n    .vue-table .arrow.asc:before {\r\n        content: \"\\f0de\";\r\n        /*\r\n        border-left: 4px solid transparent;\r\n        border-right: 4px solid transparent;\r\n        border-bottom: 4px solid #000;\r\n        */\r\n    }\r\n\r\n    .vue-table .arrow.dsc:before {\r\n        content: \"\\f0dd\";\r\n    }\r\n\r\n\r\n    .vue-table .editableField {\r\n        cursor:pointer;\r\n    }\r\n\r\n    /*.vue-table .selected-cell {\r\n        background-color: #F7C072;\r\n    }\r\n\r\n    .vue-table .selected-row {\r\n        background-color: #FAE1BE !important;\r\n    }*/\r\n</style>\r\n<script>\r\n\r\n    /* used for fixing IE problems*/\r\n    import { polyfill } from 'es6-promise'; polyfill();\r\n    import axios from 'axios';\r\n    import qs from 'qs';\r\n    import lodashorderby from 'lodash.orderby';\r\n    import lodashincludes from 'lodash.includes';\r\n    import lodashfindindex from 'lodash.findindex';\r\n\r\n\r\n    /* Field Section used for displaying and editing value of cell */\r\n    var valueFieldSection = {\r\n      template: '<span v-if=\"!enabled\" @dblclick=\"toggleInput\" class=\"editableField\">{{this.entry[this.columnname]}}</span>'+\r\n          '<div v-else-if=\"enabled\" class=\"input-group\">'+\r\n          '  <input type=\"text\" class=\"form-control\" v-model=\"datavalue\" @keyup.enter=\"saveThis\" @keyup.esc=\"cancelThis\">'+\r\n          '  <span class=\"input-group-btn\">'+\r\n          '    <button class=\"btn btn-danger\" type=\"button\" @click=\"cancelThis\" ><span class=\"fa fa-trash-alt\" aria-hidden=\"true\"></span></button>'+\r\n          '    <button class=\"btn btn-primary\" type=\"button\" @click=\"saveThis\" ><span class=\"fa fa-check\" aria-hidden=\"true\"></span></button>'+\r\n          '  </span>'+\r\n          '</div>',\r\n      props: ['entry','columnname'],\r\n      data: function () {\r\n          return {\r\n            enabled: false,\r\n            datavalue: \"\",\r\n          }\r\n      },\r\n      methods: {\r\n        saveThis: function () {\r\n            var originalValue = this.entry[this.columnname];\r\n            this.entry[this.columnname] = this.datavalue;\r\n            this.$parent.$emit('cellDataModifiedEvent', originalValue, this.datavalue, this.columnname,  this.entry);\r\n            this.enabled = !this.enabled;\r\n        },\r\n        cancelThis: function () {\r\n            this.datavalue = this.entry[this.columnname];\r\n            this.enabled = !this.enabled;\r\n        },\r\n        toggleInput: function () {\r\n            this.datavalue= this.entry[this.columnname];\r\n            this.enabled=!this.enabled;\r\n        },\r\n      }\r\n    };\r\n\r\n    export default {\r\n        name: \"VueBootstrapTable\",\r\n        components: {\r\n            'value-field-section': valueFieldSection,\r\n        },\r\n        props: {\r\n            /**\r\n             * The column titles, required\r\n             */\r\n            columns: {\r\n                type: Array,\r\n                required: true,\r\n            },\r\n            /**\r\n             * The rows, an Array of objects\r\n             */\r\n            values: {\r\n                type: Array,\r\n                required: false,\r\n            },\r\n            /**\r\n             * Enable/disable table row selection, optional, default false.\r\n             * When true, it will add a checkbox column on the left side and use the value.selected field\r\n             */\r\n            selectable: {\r\n                type: Boolean,\r\n                required: false,\r\n                default: true,\r\n            },\r\n            /**\r\n             * Enable/disable table sorting, optional, default true\r\n             */\r\n            sortable: {\r\n                type: Boolean,\r\n                required: false,\r\n                default: true,\r\n            },\r\n            /**\r\n             * Enable/disable table multicolumn sorting, optional, default false.\r\n             * Also sortable must be enabled for this function to work.\r\n             */\r\n            multiColumnSortable: {\r\n                type: Boolean,\r\n                required: false,\r\n                default: false,\r\n            },\r\n            /**\r\n             * Enable/disable input filter, optional, default false\r\n             */\r\n            showFilter: {\r\n                type: Boolean,\r\n                required: false,\r\n                default: false,\r\n            },\r\n            /**\r\n             * Define if Filter search field is to work in a case Sensitive way. Default: true\r\n             */\r\n            filterCaseSensitive: {\r\n                type: Boolean,\r\n                required: false,\r\n                default: true,\r\n            },\r\n            /**\r\n             * Enable/disable column picker to show/hide table columns, optional, default false\r\n             */\r\n            showColumnPicker: {\r\n                type: Boolean,\r\n                required: false,\r\n                default: false,\r\n            },\r\n            /**\r\n             * Enable/disable pagination for the table, optional, default false\r\n             */\r\n            paginated: {\r\n                type: Boolean,\r\n                required: false,\r\n                default: false,\r\n            },\r\n            /**\r\n             * If pagination is enabled defining the page size, optional, default 10\r\n             */\r\n            pageSize: {\r\n                type: Number,\r\n                required: false,\r\n                default: 10,\r\n            },\r\n            /**\r\n             * Setting default order column. Expected name of the column\r\n             */\r\n            defaultOrderColumn: {\r\n                type: String,\r\n                required: false,\r\n                default: null,\r\n            },\r\n            /**\r\n             * Setting default order direction. Boolean: true = ASC , false = DESC\r\n             */\r\n            defaultOrderDirection: {\r\n                type: Boolean,\r\n                required: false,\r\n                default: true,\r\n            },\r\n            /**\r\n             * If loading of table is to be done through ajax, then this object must be set\r\n             */\r\n            ajax: {\r\n                type: Object,\r\n                required: false,\r\n                default: function () {\r\n                    return {\r\n                        enabled: false,\r\n                        url: \"\",\r\n                        method: \"GET\",\r\n                        delegate: false,\r\n                        axiosConfig: {}\r\n                    }\r\n                }\r\n            },\r\n            /**\r\n             * Function to handle row clicks\r\n             */\r\n            rowClickHandler: {\r\n                type: Function,\r\n                required: false,\r\n                default: function () {}\r\n            },\r\n        },\r\n        data: function () {\r\n            return {\r\n                filteredSize: 0,\r\n                filterKey: \"\",\r\n                sortKey: [],\r\n                sortOrders: {},\r\n                sortChanged: 1,\r\n                // columnMenuOpen: false,\r\n                displayCols: [],\r\n                filteredValues: [],\r\n                rawValues: [],\r\n                page: 1,\r\n                definedPageSize: 10,\r\n                echo: 0,\r\n                loading: true,\r\n\r\n                allSelected: false,\r\n            };\r\n        },\r\n        /**\r\n         * Once mounted and ready to start\r\n         */\r\n        mounted: function () {\r\n            this.$nextTick(function () {\r\n                this.loading = true;\r\n                this.setSortOrders();\r\n                this.definedPageSize = this.pageSize;\r\n                var self = this;\r\n                //\r\n                if (this.defaultOrderColumn !== null) {\r\n                    console.log(\"setting order default\");\r\n                    self.sortKey[0] = this.defaultOrderColumn;\r\n                    if (this.defaultOrderDirection)\r\n                        self.sortOrders[this.defaultOrderColumn] = \"ASC\";\r\n                    else\r\n                        self.sortOrders[this.defaultOrderColumn] = \"DESC\";\r\n                }\r\n                // Build columns\r\n                this.columns.forEach(function (column) {\r\n                    var obj = self.buildColumnObject(column);\r\n                    self.displayCols.push(obj);\r\n                });\r\n                // Work the data\r\n                if (this.ajax.enabled) {\r\n                    if (!this.ajax.delegate) {\r\n                        // If ajax but NOT delegate\r\n                        // Perform the fetch of data now and set the raw values\r\n                        this.loading = true;\r\n                        this.fetchData(function (data) {\r\n                            self.rawValues = data.data;\r\n                        });\r\n                    } else {\r\n                        // If ajax and also delegate\r\n                        // Simply call processFilter, which will take care of the fetching\r\n                        //this.processFilter();\r\n                    }\r\n                } else {\r\n                    // Not ajax, therefore working with given elements\r\n                    // Pass the Prop values to rawValues data object.\r\n                    self.rawValues = self.values;\r\n                }\r\n            })\r\n        },\r\n        /**\r\n         * On created register on CellDataModified event\r\n         */\r\n        created: function () {\r\n            var self = this ;\r\n            this.$on('cellDataModifiedEvent', self.fireCellDataModifiedEvent);\r\n        },\r\n        /**\r\n         * On destroy unregister the event\r\n         */\r\n   